<template>
  <div class=" tw-border tw-border-solid tw-rounded-md">
    <mavon-editor
      :value="value"
      @input="em"
      style="z-index: 0; border: none"
      :boxShadow="false"
      :subfield="true"
      :autofocus="false"
      :toolbars="toolbars"
      />
  </div>
</template>

<script>
export default {

  methods: {
    em(e) {
      this.$emit('input', e)
    }
  },
  props: ['value'],

  data() {
    return {
      // value: "",

      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: false, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        navigation: true, // 
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        subfield: true, // 
        preview: true, // 
      }
    };
  },
};
</script>

